angular.module("mentor_rubric").run(["$templateCache",function(a){"use strict";a.put("/new_platform/static/mentor_rubric/nghtml/header-bar.html",'<div ng-controller="headerController">\n<div class="navbar navbar-default" role="navigation">\n  <div class="container-fluid">\n    <div class="navbar-header" style="font-weight: 400;color:white;">\n      <hamburger-toggle ng-mouseover="hamburgerHover()" ng-mouseleave="HamburgerUnhover()" ng-click="toggleState()" style="cursor: pointer;" state="tgState" class="hamburger-toggle"></hamburger-toggle>\n      <span class="navbar-brand">\n        Mentor Rubric\n      </span>\n      <div style="display: inline-block;position: relative;top: 9px;left:10px;">\n        <span ng-if="loadingMentorRubric || loadingMentorRubricNewContent">Loading...</span>\n        <span ng-if="storageAvailable == false">Please disable private browsing.</span>\n        <span ng-if="unauthorizedUser || flipbookNoContent">Content is not available.</span>\n        <span ng-if="savedNotification && !loadingMentorRubric && !loadingMentorRubricNewContent">Saving...</span>\n      </div>\n\n\n      <div class="notifications">\n        <span ng-click="$root.toggleChat();" ng-if="userrole === \'Mentor\' || userrole === \'Mentee\'" class="username">{{firstname}} {{lastname.split(\'\')[0]}}</span>\n        <span ng-click="$root.toggleChat();"  ng-if="userrole !== \'Mentor\' && userrole !== \'Mentee\'" class="username">{{firstname}} {{lastname.split(\'\')[0]}}</span>\n      </div>\n    </div>\n  </div>\n</div>\n\n\n<div class="sidenav" ng-class="{open: tgState}" ng-init="Administration = false; Reports = false;" style="font-size:14px;font-weight: 400;">\n  <ul ng-if="userrole === \'Mentor\' || userrole === \'Mentee\'" class="nav nav-pills nav-stacked">\n    <li ui-sref-active="active"><a href="/canvas/#/">Current Lesson</a></li>\n    <li ui-sref-active="active"><a href="/portfolio/#/">Portfolio</a></li>\n    <li ng-if="(match_status === \'M\' && userrole === \'Mentor\') || userrole === \'Mentee\'" ui-sref-active="active"><a href="/cchklist/list/#/">College Checklist</a></li>\n    <li><a href="/events/personal/">Calendar</a></li>\n    <!--<li><a href="/iuser/myinfo/{{persona}}/" >Profile</a></li>-->\n    <!--<li ng-show="userrole === \'Mentee\'"><a href="https://learn.imentor.org/help/article/link/imentor-curriculum">Resources</a></li>-->\n    <li ng-show="userrole !== \'Mentee\'"><a href="https://learn.imentor.org/help/article/link/home" target="_blank">Learning Center</a></li>\n    <li><a href="/message/list/">Legacy Platform</a></li>\n    <li ui-sref-active="active"><a href="https://imentor.org/privacy-policy">iMentor Privacy Policy</a></li>\n    <li ui-sref-active="active"><a  href="" ng-click="logout();">Sign out</a></li>\n  </ul>\n  <ul ng-if="userrole !== \'Mentor\' && userrole !== \'Mentee\'" class="nav nav-pills nav-stacked">\n    <li ui-sref-active="active"><a href="/canvas/#/">Current Lesson</a></li>\n    <li ui-sref-active="active"><a href="/portfolio/#/">Portfolio</a></li>\n    <li ui-sref-active="active"><a href="/lesson_mgmt/#/">Lesson Management</a></li>\n    <li><a href="/events/admin/">Calendar</a></li>\n    <li><a href="/message/list/note/">Add a Note</a></li>\n    <!--<li><a href="/iuser/backend_profile/{{persona}}/" >Profile</a></li>-->\n    <li><a ng-click="Administration = !Administration" style="cursor:pointer"><span class="fa fa-caret-down" style="position: relative;top: 3px;margin-right: 3px;font-size: 14px;"></span> Administration </a></li>\n    <ul ng-show="Administration === true" class="submenu">\n      <li><a href="/iuser/" >Users</a></li>\n      <li><a href="/partners/list/">Partner Sites</a></li>\n      <li><a href="/attendance/">Attendance</a></li>\n      <!--<li><a href="/survey/">Surveys</a></li>-->\n      <li><a href="/new_surveys/">Surveys</a></li>\n      <li ng-show="subdomain === \'nyc\' || subdomain == \'chicago\' || subdomain == \'platformtraining\' || subdomain == \'127\' || subdomain == \'bayarea\' || subdomain == \'cnm\'"><a href="/enrollment/">Mentee Enrollment</a></li>\n      <li ng-show="subdomain === \'platformtraining\'"><a href="/mentor_rubric/">Mentor Rubric</a></li>\n      <!--<li><a href="/reporting/manage_alerts/">Alerts</a></li>-->\n    </ul>\n    <li><a ng-click="Reports = !Reports" style="cursor:pointer"><span class="fa fa-caret-down" style="position: relative;top: 3px;margin-right: 7px;font-size: 14px;"></span>Reports</a></li>\n    <ul ng-show="Reports === true" class="submenu">\n      <li><a href="/snapshot/">Snapshot</a></li>\n      <li ng-show="isOnPartnerListTableau && (subdomain == \'nyc\' || subdomain == \'chicago\' || subdomain == \'bayarea\')"><a ng-href="{{programUrl}}" target="_blank" style="cursor: pointer;" ng-click="getProgramParticipation(subdomain);">S.T.E.V.E.</a></li>\n      <li ng-show="isOnPartnerListTableau && (subdomain != \'nyc\' && subdomain != \'chicago\' && subdomain != \'bayarea\' && subdomain == \'127\') && userrole != \'Partner Admin\'"><a ng-href="{{programUrl}}" target="_blank" style="cursor: pointer;" ng-click="getProgramParticipation(subdomain);">Participation History</a></li>\n      <li><a href="/flipbook/">Flipbook</a></li>\n      <li><a href="/reporting/intake_application_tracker/">Intake App Tracker</a></li>\n      <li><a href="/iuser/screening_tracker/">Screening Tracker</a></li>\n      <!--<li><a href="/reporting/participation_tracker/">MPT</a></li>-->\n      <!--<li><a href="/reporting/email_tracker/">Email Tracker</a></li>-->\n      <!--<li><a href="/reporting/email_log/">Email Log</a></li>-->\n      <li><a href="/reporting/event_tracker/">Event Tracker</a></li>\n      <li><a href="/reporting/event_log/">Event Log</a></li>\n      <!--<li><a href="/ratings/tracker/pairs/2/">Ratings Tracker</a></li>-->\n      <li><a href="/notes/support_tracker_pair/">Support Tracker</a></li>\n      <li><a href="/notes/note_log/">Note Log</a></li>\n    </ul>\n    <li><a href="https://learn.imentor.org/" target="_blank">Learning Center</a></li>\n    <li><a href="/message/list/">Legacy Platform</a></li>\n    <li ui-sref-active="active"><a href="https://imentor.org/privacy-policy">iMentor Privacy Policy</a></li>\n    <li ui-sref-active="active"><a href="" ng-click="logout();">Sign out</a></li>\n  </ul>\n</div>\n</div>\n'),a.put("/new_platform/static/mentor_rubric/nghtml/mentor_rubric.html",'<div class="UnitContainer" id="top" style="">\n  <div>\n    <h2 class="admin-heading" style="padding-bottom: 15px; padding-bottom: 30px;" >Evaluate your mentors</h2>\n\n    <div ng-if="showDropdownYears" style="position: relative;top:0px; display: inline-block; margin-right: 10px;" class="mobileCont">\n\n      <div\n              isteven-multi-select\n              input-model="vm.program_years"\n              output-model="vm.selectedYears"\n              button-label="program_year_description"\n              item-label="program_year_description"\n              tick-property="ticked"\n              class="partner-sites"\n              max-height="200px"\n              max-labels="1"\n              on-item-click="vm.updateYear()"\n              on-select-all="vm.updateYearAll()"\n              on-select-none="vm.resetYear()"\n              on-reset="vm.resetYear()"\n              translation="vm.programYearsLang"\n              helper-elements="all filter none"\n              selection-mode="single"\n      >\n      </div>\n      <div\n              isteven-multi-select\n              input-model="vm.partner_info"\n              output-model="vm.selectedPartnerSites"\n              button-label="name"\n              item-label="name"\n              tick-property="ticked"\n              class="partner-sites"\n              max-height="200px"\n              max-labels="1"\n              on-item-click="vm.updatePartner()"\n              on-select-all="vm.updatePartnerAll()"\n              on-select-none="vm.resetPartner()"\n              on-reset="vm.resetPartner()"\n              translation="vm.partnerSitesLang"\n              helper-elements="all filter none"\n              style="margin-left: 10px;" >\n      </div>\n      <div class="refresh">\n        <button ng-click="vm.updatePartner()" class="icon" ng-disabled="vm.apiCall" ng-hide="vm.selectedPartnerSites.length === 0 "><i class="fa fa-refresh"></i></button>\n      </div>\n    </div>\n\n    <div class="rubric-criteria" >\n       <!-- Nav tabs -->\n      <div class="info-header" role="tablist">Rubric Criteria</div>\n  <ul class="nav nav-tabs  nav-justified" role="tablist">\n    <li role="presentation" class="active"><a href="#strenghts-based" aria-controls="strenghts-based" role="tab" data-toggle="tab">Strengths Based</a></li>\n    <li role="presentation"><a href="#resources" aria-controls="resources" role="tab" data-toggle="tab">Resources</a></li>\n    <li role="presentation"><a href="#response-time" aria-controls="response-time" role="tab" data-toggle="tab">Response Time</a></li>\n    <li role="presentation"><a href="#curriculum" aria-controls="curriculum" role="tab" data-toggle="tab">Curriculum</a></li>\n  </ul>\n\n  <!-- Tab panes -->\n  <div class="tab-content">\n    <div role="tabpanel" class="tab-pane active" id="strenghts-based">\n      <p class="rubric-criteria-title">Mentor takes a strengths-based stance regarding their mentee.</p>\n       <p>\n         <div class="row1">\n        <div class="col-md-4 col-lg-4 criteria-col">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >1</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Not Meeting Expectations</p>\n                <p>Complains about mentee without an effort to maintain positivity about mentee potential. Makes negative assumptions about mentee motivations or behavior without sufficient information.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4 criteria-col">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >2</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Meeting Expectations</p>\n                <p>Mentor makes an effort to identify and capitalize on the strengths of the mentee regardless of the context and demonstrates persistence and positivity in the face of challenging situations with their mentee.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4 criteria-col">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >3</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Exceeding Expectations</p>\n                <p>Mentor demonstrates an extraordinary ability to identify and capitalize on the strengths of the mentee regardless of the context and demonstrates persistence and positivity in the face of challenging situations with their mentee.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n    </div>\n      </p>\n    </div>\n    <div role="tabpanel" class="tab-pane" id="resources">\n      <p class="rubric-criteria-title">Mentor supports mentee in utilizing all resources, when appropriate (including reaching out to their staff member or other external resources).</p>\n       <p>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >1</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Not Meeting Expectations</p>\n                <p>When opportunities to identify additional support for a mentee emerge, mentor does not utilize appropriate resources.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >2</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Meeting Expectations</p>\n                <p>When appropriate, mentor identifies additional support structures for the mentee based on mentees need (e.g. family, guidance counselor, etc.).</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >3</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Exceeding Expectations</p>\n                <p>Mentor takes initiative beyond locating an individual to help his/her mentee and identifies resources based on a mentees need and specifically aids in the mentees addressing challenges or answering difficult questions.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n      </p>\n    </div>\n    <div role="tabpanel" class="tab-pane" id="response-time">\n      <p class="rubric-criteria-title">Mentor responds to staff member in a timely fashion.</p>\n       <p>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >1</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Not Meeting Expectations</p>\n                <p>Mentor consistently does not respond to staff member within a week of being contacted.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >2</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Meeting Expectations</p>\n                <p>Mentor, on average, responds to staff member within a week of being contacted for any programmatic reasons.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >3</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Exceeding Expectations</p>\n                <p>Mentor consistently responds to any staff member contacts within 48 hours.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n      </p>\n    </div>\n    <div role="tabpanel" class="tab-pane" id="curriculum">\n      <p class="rubric-criteria-title">Mentor models effective use of canvas prompt curriculum.</p>\n       <p>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >1</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Not Meeting Expectations</p>\n                <p>Mentor consistently does not adhere to the canvas prompt guidelines.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >2</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Meeting Expectations</p>\n                <p>Mentor adheres to canvas prompt guidelines on a regular basis and communicates in an appropriate way.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n        <div class="col-md-4 col-lg-4">\n          <table>\n            <tr>\n              <td class="rubric-criteria-value"><div >3</div></td>\n              <td>\n                <p class="rubric-criteria-description-title">Exceeding Expectations</p>\n                <p>Mentor includes more high-quality and informative detail in his/her communications to his/her mentee than is suggested by the canvas prompt guidelines.</p>\n              </td>\n            </tr>\n          </table>\n        </div>\n      </p>\n    </div>\n  </div>\n    </div>\n\n  </div>\n  <div style="position: relative; top:0px;" class="mobileCont">\n    <div ng-show="vm.selectedPartnerSites.length>0">\n      <div class="row filter-buttons" >\n        <div class="col-md-12 col-lg-12">\n          \n          <input class="search" placeholder="Search" ng-model="vm.searchString">\n          <i class="fa fa-search fa-flip-horizontal" aria-hidden="true"></i>\n        </div>\n      </div>\n      <div class="margin-15"></div>\n      <table class="table table-striped new-content" ng-show="loadingMentorRubricNewContent === false && vm.new_content.length>0 && vm.filteredItems.length>0" id="rubric-table">\n        <thead style="">\n        <tr class="table-header table-header-top">\n          <th colspan="12" class="text-bottom manage-col">\n            <span href class="pull-right" style="padding: 3px 10px;" ng-click="">Showing {{vm.new_content.length}} mentors</span>\n\n          </th>\n        </tr>\n          <tr style="background-color: #4a4a4a;"  fsm-sticky-header="vm.useFixedHeader" scroll-body="#rubric-table" scroll-stop=\'35\'  fsm-z-index="9">\n            <!--<th rowspan="" class="text-bottom manage-col">\n              <span dropdown on-toggle="toggled(open)" class="pull-left dropdown"\n                >\n                <a href dropdown-toggle style="color: white;" ng-class="{\'disabled\': !vm.selectedContent}">\n                  Manage <i class="fa fa-wrench"></i>\n                </a>\n              </span>\n            </th>-->\n            <th>Mentor First</th>\n            <th>Mentor Last</th>\n            <th>Mentee First</th>\n            <th>Mentee Last</th>\n            <th class="drop-down-column">Strengths Based</th>\n            <th class="drop-down-column">Resources</th>\n            <th class="drop-down-column">Response Time</th>\n            <th class="drop-down-column">Curriculum</th>\n            <th style="text-align: center;">Complete</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr  ng-repeat="content in  (vm.filteredItems = (vm.new_content  | filter:vm.searchString))"   ng-class="{\'selected\': vm.selectedContent == content, \'unselected\': vm.selectedContent != content}">\n            <!--<td class="text-center manage-col"  ng-click="vm.updateContentSelection(content)"\n              >\n              <span class="glyphicon glyphicon-ok"  ng-class="{\'selected\': vm.selectedContent == content, \'unselected\': vm.selectedContent != content}" />\n              </td>-->\n              <td><a ng-href="{{content.mentor_profile_link}}" target="_blank">{{content.mentor_first_name}}</a></td>\n              <td><a ng-href="{{content.mentor_profile_link}}" target="_blank">{{content.mentor_last_name}}</a></td>\n              <td><a ng-href="{{content.mentee_profile_link}}" target="_blank">{{content.mentee_first_name}}</a></td>\n              <td><a ng-href="{{content.mentee_profile_link}}" target="_blank">{{content.mentee_last_name}}</a></td>\n              <td>\n                <select ng-model="content.rubric_strengths_based" ng-options="option.id as option.label for option in vm.options"  class="selectDefault " ng-class="{\'selectOne\' : content.rubric_strengths_based == 1, \'selectTwo\' : content.rubric_strengths_based == 2, \'selectThree\' : content.rubric_strengths_based == 3}"  ng-change="content.changed=true;">\n                </select>              \n              </td>\n              <td>\n                <select ng-model="content.rubric_resources" ng-options="option.id as option.label for option in vm.options"  class="selectDefault " ng-class="{\'selectOne\' : content.rubric_resources == 1, \'selectTwo\' : content.rubric_resources == 2, \'selectThree\' : content.rubric_resources == 3}"  ng-change="content.changed=true;">\n                </select>\n                \n              </td>\n              <td>\n                <select ng-model="content.rubric_response_time" ng-options="option.id as option.label for option in vm.options"  class="selectDefault " ng-class="{\'selectOne\' : content.rubric_response_time == 1, \'selectTwo\' : content.rubric_response_time == 2, \'selectThree\' : content.rubric_response_time == 3}"  ng-change="content.changed=true;">\n                </select>\n                \n              </td>\n              <td>\n                <select ng-model="content.rubric_curriculum" ng-options="option.id as option.label for option in vm.options"  class="selectDefault " ng-class="{\'selectOne\' : content.rubric_curriculum == 1, \'selectTwo\' : content.rubric_curriculum == 2, \'selectThree\' : content.rubric_curriculum == 3}"  ng-change="content.changed=true;">\n                </select>\n        \n              </td>\n              <td style="text-align: center;">\n                <button ng-show="vm.showSaved(content)" class="btn  btn-default btn-green" >Saved</button>\n                <button ng-show="vm.showSubmit(content)" ng-click="vm.updateData(content);" class="btn btn-default btn-gray">Submit</button>\n              </td>\n            </tr>\n          </tbody>\n        </table>\n      </div>\n    </div>\n    \n  </div>')}]);